import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import { useSnackbar } from 'notistack';
import {
  Avatar,
  Box,
  Button,
  CircularProgress,
  Dialog,
  Grid,
  Link,
  Typography,
  colors,
  makeStyles
} from '@material-ui/core';
import {
  Archive as ArchiveIcon,
  Users as UsersIcon,
  Layout as LayoutIcon,
  Download as DownloadIcon,
  CheckSquare as CheckIcon,
} from 'react-feather';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import axios from 'src/utils/axios';
import { API_BASE_URL } from 'src/config';
import { JOB } from 'src/config/endpoint';
import {
  deleteCard,
  updateCard,
  completeCard,
} from 'src/actions/kanbanActions';
import StackAvatars from 'src/components/StackAvatars';
import getInitials from 'src/utils/getInitials';
import Details from './Details';
import Checklist from './Checklist';
import ChooseService from './ChooseService';
import ActionButton from './ActionButton';
import ChooseMember from './ChooseMember';
import ChooseCustomer from './ChooseCustomer';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(3)
  },
  listName: {
    fontWeight: theme.typography.fontWeightMedium
  },
  avatar: {
    marginTop: 5,
    backgroundColor: colors.red[500],
    color: colors.common.white
  },
  checklist: {
    '& + &': {
      marginTop: theme.spacing(3)
    }
  }
}));

function CardEditModal({
  card,
  className,
  list,
  onClose,
  open,
  ...rest
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const [isOpenedChooseService, setOpenedChooseService] = useState(false);
  const [isOpenedChooseMember, setOpenedChooseMember] = useState(false);
  const [isOpenedChooseCustomer, setOpenedChooseCustomer] = useState(false);
  const [job, setJob] = useState(card);
  const [coverFile, setCoverFile] = useState(null);
  const { members } = useSelector((state) => state.kanban);
  const [workers, setWorkers] = useState([]);
  const [report, setReport] = useState(card.report);
  const [generating, setGenerating] = useState(false);

  useEffect(() => {
    if (job.workers) {
      setWorkers(job.workers.map((memberId) => members.byId[memberId]));
    }
  }, [job.workers]);

  const handleSave = async () => {
    try {
      await dispatch(updateCard(card._id, job, coverFile));
      enqueueSnackbar('Job Updated', {
        variant: 'success'
      });
    } catch (error) {
      enqueueSnackbar('Ooops!', {
        variant: 'error'
      });
    }
  };

  const handleDelete = async () => {
    try {
      await dispatch(deleteCard(card._id));
      enqueueSnackbar('Card archived', {
        variant: 'success'
      });
    } catch (error) {
      enqueueSnackbar('Ooops!', {
        variant: 'error'
      });
    }
  };

  const handleOpenChooseService = async () => {
    setOpenedChooseService(true);
  };

  const handleOpenChooseCustomer = async () => {
    setOpenedChooseCustomer(true);
  };

  const handleOpenChooseMember = async () => {
    setOpenedChooseMember(true);
  };

  const handleCloseChooseService = () => {
    setOpenedChooseService(false);
  };

  const handleCloseChooseCustomer = () => {
    setOpenedChooseCustomer(false);
  };

  const handleCloseChooseMember = () => {
    setOpenedChooseMember(false);
  };

  const handleGeneratePDF = () => {
    setGenerating(true);

    axios
      .get(`${API_BASE_URL}/${JOB.GENERATE_PDF}/${job._id}`)
      .then((response) => {
        setGenerating(false);
        setReport(response.data);
      })
      .catch((error) => {
        // Handle error
        setGenerating(false);
        const { response } = error;
        return { error: response.data };
      });
  };

  const handleDownload = () => {
    window.open(report);
  };

  const handleComplete = async () => {
    if (job.check_list) {
      const checkList = [...job.check_list];
      for (let i = 0; i < checkList.length; i++) {
        checkList[i] = { ...checkList[i], state: 'completed' };
      }
      setJob({
        ...job,
        check_list: checkList,
        is_active: false,
        completed: true,
      });
    }

    try {
      await dispatch(completeCard(card._id, job));
      enqueueSnackbar('Job Completed', {
        variant: 'success'
      });
    } catch (error) {
      enqueueSnackbar('Ooops!', {
        variant: 'error'
      });
    }
  };

  return (
    <Dialog
      onClose={onClose}
      open={open}
      maxWidth="md"
      fullWidth
      disableBackdropClick
      {...rest}
    >
      <div className={classes.root}>
        <Box
          display="flex"
          justifyContent="space-between"
        >
          <Typography
            variant="body2"
            color="textSecondary"
          >
            in list
            {' '}
            <span className={classes.listName}>
              {list.title}
            </span>
          </Typography>
          <Box mt={1}>
            <Button
              size="small"
              variant="contained"
              color="secondary"
              ml={2}
              onClick={handleSave}
            >
              Save
            </Button>
            <Button
              size="small"
              onClick={onClose}
              ml={2}
            >
              Cancel
            </Button>
          </Box>
        </Box>
        <Grid
          container
          spacing={5}
        >
          <Grid
            item
            xs={12}
            sm={8}
          >
            <Details
              card={job}
              list={list}
              handleUpdateTitle={(title) => { setJob({ ...job, title }); }}
              handleUpdateNote={(note) => { setJob({ ...job, note }); }}
              handleUpdateAddress={(address) => { setJob({ ...job, address }); }}
              handleUpdateZipCode={(zip_code) => { setJob({ ...job, zip_code }); }}
              handleUpdateCover={(cover, file) => {
                setJob({ ...job, cover });
                setCoverFile(file);
              }}
            />
            { job.customer && (
            <Box mt={3}>
              <Typography
                variant="h4"
                color="textPrimary"
              >
                Customer
              </Typography>
              <Box
                mt={1}
                display="flex"
              >
                <Avatar
                  className={classes.avatar}
                >
                  {getInitials(`${job.customer.first_name} ${job.customer.last_name}`)}
                </Avatar>
                <Box ml={2}>
                  <Link
                    variant="subtitle2"
                    color="textPrimary"
                    underline="none"
                    to="#"
                  >
                    {`${job.customer.first_name} ${job.customer.last_name}`}
                  </Link>
                  <Typography
                    variant="body2"
                    color="textSecondary"
                  >
                    {job.customer.email}
                  </Typography>
                </Box>
              </Box>
            </Box>
            )}
            {/* {workers && workers.length > 0 && (
              <Box mt={3}>
                <Typography
                  variant="h4"
                  color="textPrimary"
                >
                  Worker
                </Typography>
                <Box mt={2}>
                  <StackAvatars
                    avatars={workers.map((member) => member.profile_picture)}
                    mt={2}
                  />
                </Box>
              </Box>
            )} */}
            {job.check_list && (
              <Box mt={3}>
                <Typography
                  variant="h4"
                  color="textPrimary"
                >
                  Check List
                </Typography>
                <Box mt={2}>
                  <Checklist
                    card={job}
                    checklist={job.check_list}
                    className={classes.checklist}
                    handleUpdateCheckList={(check_list) => { setJob({ ...job, check_list }); }}
                  />
                </Box>
              </Box>
            )}
          </Grid>
          <Grid
            item
            xs={12}
            sm={4}
          >
            <Typography
              variant="overline"
              color="textSecondary"
            >
              Actions
            </Typography>
            {/* <ActionButton
              icon={UsersIcon}
              onClick={handleOpenChooseCustomer}
            >
              Customer
            </ActionButton> */}
            <ActionButton
              icon={UsersIcon}
              onClick={handleOpenChooseMember}
            >
              Workers
            </ActionButton>
            {/* {!job.is_active
              ? (
                <ActionButton
                  icon={LayoutIcon}
                  onClick={handleOpenChooseService}
                >
                  Choose Service
                </ActionButton>
              )
              : (
                <ActionButton
                  icon={LayoutIcon}
                  onClick={handleOpenChooseService}
                  disabled
                >
                  Choose Service
                </ActionButton>
              )} */}
            {generating
              ? (
                <ActionButton>
                  <CircularProgress
                    size={20}
                  />
                  {' '}
                  Generating Report
                </ActionButton>
              ) : (
                <ActionButton
                  icon={PictureAsPdfIcon}
                  onClick={handleGeneratePDF}
                >
                  Generate Report
                </ActionButton>
              )}
            { report && (
            <ActionButton
              icon={DownloadIcon}
              onClick={handleDownload}
            >
              Download Report
            </ActionButton>
            )}
            <ActionButton
              icon={CheckIcon}
              onClick={handleComplete}
            >
              Complete
            </ActionButton>
            <ActionButton
              icon={ArchiveIcon}
              onClick={handleDelete}
            >
              Archive
            </ActionButton>
          </Grid>
        </Grid>
        <ChooseService
          open={isOpenedChooseService}
          onClose={handleCloseChooseService}
          onChooseService={(service) => {
            setJob({ ...job, check_list: service.check_list });
          }}
          card={job}
          list={list}
        />
        <ChooseMember
          open={isOpenedChooseMember}
          onClose={handleCloseChooseMember}
          onChooseMembers={(choosedMembers) => {
            setJob({ ...job, workers: choosedMembers });
          }}
          card={job}
          list={list}
        />
        <ChooseCustomer
          open={isOpenedChooseCustomer}
          onClose={handleCloseChooseCustomer}
          onChooseCustomer={(choosenCustomer) => {
            setJob({ ...job, customer: choosenCustomer });
          }}
          card={job}
          list={list}
        />
      </div>
    </Dialog>
  );
}

CardEditModal.propTypes = {
  card: PropTypes.object.isRequired,
  className: PropTypes.string,
  list: PropTypes.object.isRequired,
  onClose: PropTypes.func,
  open: PropTypes.bool
};

CardEditModal.defaultProps = {
  open: false,
  onClose: () => {}
};

export default CardEditModal;
