import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import {
  Box,
  Dialog,
  makeStyles
} from '@material-ui/core';
import PropTypes from 'prop-types';
import axios from 'src/utils/axios';
import clsx from 'clsx';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import { API_BASE_URL } from 'src/config';
import { WORKER } from 'src/config/endpoint';
import Results from './Results';

const useStyles = makeStyles((theme) => ({
  root: {
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: 100
  }
}));

function ChooseMember({
  className,
  card,
  onChooseMembers,
  onClose,
  open
}) {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [workers, setWorkers] = useState(null);

  const getWorkers = useCallback(() => {
    axios
      .get(`${API_BASE_URL}/${WORKER.GET_ALL}`)
      .then((response) => {
        if (isMountedRef.current) {
          setWorkers(response.data.workers);
        }
      })
      .catch((error) => {
        // Handle error
        console.log('error', error.message);
      });
  }, [isMountedRef]);

  useEffect(() => {
    getWorkers();
  }, [getWorkers]);

  if (!workers) {
    return null;
  }


  return (
    <Dialog
      className={clsx(classes.root, className)}
      onClose={onClose}
      open={open}
      maxWidth="md"
      fullWidth
    >
      {workers && (
        <Box mt={3}>
          <Results
            customers={workers}
            selCustomers={card.workers || []}
            onChooseWorkers={(selectedWorkers) => {
              onChooseMembers(selectedWorkers);
              onClose();
            }}
          />
        </Box>
      )}
    </Dialog>
  );
}

ChooseMember.propTypes = {
  className: PropTypes.string,
  onChooseMembers: PropTypes.func,
  card: PropTypes.object,
  onClose: PropTypes.func,
  open: PropTypes.bool
};

ChooseMember.defaultProps = {
  open: false,
  onClose: () => {}
};

export default ChooseMember;
