import React, {
  useState,
  useEffect
} from 'react';
import PropTypes from 'prop-types';
import { v4 as uuidv4 } from 'uuid';
import clsx from 'clsx';
import {
  Box,
  Button,
  LinearProgress,
  Typography,
  SvgIcon,
  makeStyles
} from '@material-ui/core';
import {
  List as ListIcon,
} from 'react-feather';
import CheckItem from './CheckItem';
import CheckItemAdd from './CheckItemAdd';

const useStyles = makeStyles((theme) => ({
  root: {},
  listIcon: {
    marginRight: theme.spacing(3)
  }
}));

function Checklist({
  card,
  checklist,
  className,
  handleUpdateCheckList,
  ...rest
}) {
  const classes = useStyles();
  // const [checkList, setCheckList] = useState(checklist);
  const [isOpenedCheckItemAdd, setOpenedCheckItemAdd] = useState(false);

  // useEffect(() => {
  //   if (checkList && checkList.length > 0) {
  //     handleUpdateCheckList(checkList);
  //   }
  // }, [checkList]);

  const handleCheckItemEdit = (index, updatedItem) => {
    if (checklist && checklist.length > 0) {
      const updatedList = [...checklist];
      updatedList[index] = updatedItem;
      handleUpdateCheckList(updatedList);
    }
  };

  const handleCheckItemAdd = (checkItem) => {
    const checkedItem = { ...checkItem, _id: uuidv4() };
    handleUpdateCheckList([...checklist, checkedItem]);
  };

  const handleCheckItemRemove = (index) => {
    const updatedList = [...checklist];
    updatedList.splice(index, 1);
    handleUpdateCheckList(updatedList);
  };

  const handleCheckItemComplete = (index) => {
    const checkedItem = { ...checklist[index] };
    if (checklist[index] && checklist[index].state !== 'completed') {
      Object.assign(checkedItem, { state: 'completed' });
    } else {
      Object.assign(checkedItem, { state: 'none' });
    }
    const updatedList = [...checklist];
    updatedList[index] = checkedItem;
    handleUpdateCheckList(updatedList);
  };

  const handleCheckItemMoveUp = (index) => {
    if (index === 0) {
      return;
    }
    const updatedList = [...checklist];
    updatedList[index - 1] = checklist[index];
    updatedList[index] = checklist[index - 1];
    handleUpdateCheckList(updatedList);
  };

  const handleCheckItemMoveDown = (index) => {
    if (index === checklist.length - 1) {
      return;
    }
    const updatedList = [...checklist];
    updatedList[index + 1] = checklist[index];
    updatedList[index] = checklist[index + 1];
    handleUpdateCheckList(updatedList);
  };

  const handleAddCheckItem = () => {
    setOpenedCheckItemAdd(true);
  };

  const handleCloseCheckItemAdd = () => {
    setOpenedCheckItemAdd(false);
  };

  const totalCheckItems = checklist.length;
  const completedCheckItems = (checklist.filter((checkItem) => checkItem.state === 'completed')).length;
  const completePercentage = totalCheckItems === 0
    ? 100
    : (completedCheckItems / totalCheckItems) * 100;

  return (
    <div
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Box
        display="flex"
      >
        <SvgIcon
          fontSize="small"
          color="action"
          className={classes.listIcon}
        >
          <ListIcon />
        </SvgIcon>
      </Box>
      <Box
        mt={1}
        display="flex"
        alignItems="center"
      >
        <Typography
          variant="caption"
          color="textSecondary"
        >
          {parseInt(completePercentage, 10)}
          %
        </Typography>
        <Box
          ml={2}
          flexGrow={1}
        >
          <LinearProgress
            variant="determinate"
            value={completePercentage}
            color="secondary"
          />
        </Box>
      </Box>
      <Box mt={3}>
        {checklist.map((checkItem, index) => (
          <CheckItem
            checkItem={checkItem}
            key={checkItem._id}
            onRemove={() => handleCheckItemRemove(index)}
            onCheckItem={() => handleCheckItemComplete(index)}
            onMoveUp={() => handleCheckItemMoveUp(index)}
            onMoveDown={() => handleCheckItemMoveDown(index)}
            onEdit={(updatedItem) => handleCheckItemEdit(index, updatedItem)}
          />
        ))}
      </Box>
      <Box
        mt={1}
        ml={6}
      >
        <Button
          variant="outlined"
          size="small"
          onClick={handleAddCheckItem}
        >
          Add an item
        </Button>
        <CheckItemAdd
          open={isOpenedCheckItemAdd}
          onClose={handleCloseCheckItemAdd}
          addItem={(checkedItem) => { handleCheckItemAdd(checkedItem); }}
        />
      </Box>
    </div>
  );
}

Checklist.propTypes = {
  className: PropTypes.string,
  card: PropTypes.object.isRequired,
  checklist: PropTypes.array.isRequired,
  handleUpdateCheckList: PropTypes.func,
};

export default Checklist;
