import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Box,
  IconButton,
  SvgIcon,
  Typography,
  makeStyles
} from '@material-ui/core';
import {
  Trash as TrashIcon,
  Edit as EditIcon,
  Check as CheckIcon,
  MinusSquare as MinusSquareIcon,
  ArrowDown as ArrowDownIcon,
  ArrowUp as ArrowUpIcon
} from 'react-feather';
import CheckItemDetail from './CheckItemDetail';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(1),
    display: 'flex',
    alignItems: 'flex-start',
    borderRadius: theme.shape.borderRadius,
    '&:hover': {
      backgroundColor: theme.palette.background.dark,
      '& $deleteButton': {
        visibility: 'visible'
      }
    }
  },
  checkbox: {
    marginRight: theme.spacing(0)
  },
  name: {
    flexGrow: 1,
    cursor: 'pointer',
    minHeight: 32
  },
  deleteButton: {
    visibility: 'hidden'
  }
}));

function CheckItem({
  checkItem,
  className,
  editing,
  onRemove,
  onEdit,
  onMoveDown,
  onMoveUp,
  onCheckItem,
  ...rest
}) {
  const classes = useStyles();
  const [isOpenedCheckItemDetail, setOpenedCheckItemDetail] = useState(false);

  const handleEdit = () => {
    setOpenedCheckItemDetail(true);
  };

  const handleCloseCheckItemDetail = () => {
    setOpenedCheckItemDetail(false);
  };

  return (
    <div
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Box
        display="flex"
        alignItems="center"
        flexGrow={1}
      >
        <Typography
          color="textPrimary"
          variant="body1"
          className={classes.name}
          style={{ textDecoration: checkItem.state === 'completed' ? 'line-through' : 'none' }}
        >
          {checkItem.description}
        </Typography>
        <IconButton
          className={classes.checkbox}
          onClick={() => { onCheckItem(); }}
        >
          <SvgIcon
            fontSize="small"
          >
            {(checkItem.state === 'completed')
              ? <MinusSquareIcon />
              : <CheckIcon />}

          </SvgIcon>
        </IconButton>
        <IconButton
          className={classes.checkbox}
          onClick={handleEdit}
        >
          <SvgIcon
            fontSize="small"
          >
            <EditIcon />
          </SvgIcon>
        </IconButton>
        <IconButton
          className={classes.checkbox}
          onClick={() => { onMoveUp(); }}
        >
          <SvgIcon
            fontSize="small"
          >
            <ArrowUpIcon />
          </SvgIcon>
        </IconButton>
        <IconButton
          className={classes.checkbox}
          onClick={() => { onMoveDown(); }}
        >
          <SvgIcon
            fontSize="small"
          >
            <ArrowDownIcon />
          </SvgIcon>
        </IconButton>
        <IconButton
          className={classes.checkbox}
          onClick={() => { onRemove(); }}
        >
          <SvgIcon
            fontSize="small"
          >
            <TrashIcon />
          </SvgIcon>
        </IconButton>
      </Box>
      <CheckItemDetail
        open={isOpenedCheckItemDetail}
        onClose={handleCloseCheckItemDetail}
        checkItem={checkItem}
        updateItem={(checkedItem) => {
          onEdit(checkedItem);
        }}
      />
    </div>
  );
}

CheckItem.propTypes = {
  checkItem: PropTypes.object.isRequired,
  className: PropTypes.string,
  editing: PropTypes.bool,
  onRemove: PropTypes.func,
  onCheckItem: PropTypes.func,
  onEdit: PropTypes.func,
  onMoveDown: PropTypes.func,
  onMoveUp: PropTypes.func,
};

CheckItem.defaultProps = {
  editing: false,
  onRemove: () => {},
  onCheckItem: () => {},
  onEdit: () => {},
  onMoveDown: () => {},
  onMoveUp: () => {},
};

export default CheckItem;
