import React, { useRef } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import {
  Box,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import Images from 'src/theme/images';

const useStyles = makeStyles(() => ({
  root: {},
  photo: {
    width: '100%'
  },
  newPhoto: {
    width: '100%',
    maxWidth: 150,
  },
  address: {
    maxWidth: 300,
    width: '100%',
  }
}));

function Details({
  card,
  className,
  list,
  handleUpdateTitle,
  handleUpdateNote,
  handleUpdateCover,
  handleUpdateAddress,
  handleUpdateZipCode,
  ...rest
}) {
  const classes = useStyles();
  const pickerInput = useRef();

  const _onImageInputHandler = (event) => {
    event.preventDefault();
    const _file = event.target.files[0];
    if (_file) {
      const url = URL.createObjectURL(_file);
      handleUpdateCover(url, _file);
    }
  };

  return (
    <div
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Box mt={1}>
        <Typography
          variant="h4"
          color="textPrimary"
        >
          Title
        </Typography>
        <Box mt={2}>
          <TextField
            variant="outlined"
            fullWidth
            defaultValue={card.title}
            onChange={(event) => handleUpdateTitle(event.target.value)}
          />
        </Box>
      </Box>
      <Box mt={3}>
        <Typography
          variant="h4"
          color="textPrimary"
        >
          Note
        </Typography>
        <Box mt={2}>
          <TextField
            multiline
            rows={6}
            fullWidth
            variant="outlined"
            onChange={(event) => handleUpdateNote(event.target.value)}
            placeholder="Write down note"
            defaultValue={card.note}
          />
        </Box>
      </Box>
      {/* <Box
        mt={3}
        display="flex"
      >
        <Box
          mr={2}
          width={300}
          className={classes.address}
        >
          <Typography
            variant="h4"
            color="textPrimary"
          >
            Address
          </Typography>
          <TextField
            variant="outlined"
            fullWidth
            onChange={(event) => handleUpdateAddress(event.target.value)}
            placeholder="Address"
            defaultValue={card.address}
          />
        </Box>
        <Box
          mr={2}
        >
          <Typography
            variant="h4"
            color="textPrimary"
          >
            Post Code
          </Typography>
          <TextField
            variant="outlined"
            onChange={(event) => handleUpdateZipCode(event.target.value)}
            placeholder="Post Code"
            defaultValue={card.zip_code}
          />
        </Box>
      </Box> */}
      <Box mt={3}>
        <Typography
          variant="h4"
          color="textPrimary"
        >
          Cover Image
        </Typography>
        <Box
          mt={2}
        >
          <Box
            className={classes.photoImg}
            onClick={() => {
              pickerInput.current.click();
            }}
          >
            { card.cover ? (
              <img
                className={classes.photo}
                src={card.cover}
                alt="Add New"
              />
            ) : (
              <img
                className={classes.newPhoto}
                src={Images.AddFileIcon}
                alt="Add New"
              />
            )}
          </Box>
          <input
            ref={pickerInput}
            type="file"
            style={{
              display: 'none'
            }}
            onChange={(e) => { _onImageInputHandler(e); }}
          />
        </Box>
      </Box>
    </div>
  );
}

Details.propTypes = {
  card: PropTypes.object.isRequired,
  className: PropTypes.string,
  handleUpdateTitle: PropTypes.func,
  handleUpdateNote: PropTypes.func,
  handleUpdateCover: PropTypes.func,
  handleUpdateAddress: PropTypes.func,
  handleUpdateZipCode: PropTypes.func,
  list: PropTypes.object.isRequired
};

export default Details;
