/* eslint-disable prefer-destructuring */
/* eslint-disable react/no-array-index-key */
/* eslint-disable max-len */
import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import {
  Box,
  Button,
  CardMedia,
  Chip,
  CircularProgress,
  Dialog,
  Divider,
  Grid as GridRow,
  Input,
  Switch,
  TextareaAutosize,
  TextField,
  Typography,
  makeStyles,
  withStyles,
} from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import {
  FORMS, FORM_DESCRIPTIONS, TABLE_COLUMNS, TABLE_DATA, CHIPS, LIST_COLUMN
} from 'src/constants';
import Images from 'src/theme/images';
import { EditingState } from '@devexpress/dx-react-grid';
import {
  Grid,
  Table,
  TableHeaderRow,
  TableEditRow,
  TableEditColumn,
} from '@devexpress/dx-react-grid-material-ui';
import axios from 'src/utils/axios';
import { API_BASE_URL2 } from 'src/config';
import { FILE } from 'src/config/endpoint';

const useStyles = makeStyles((theme) => ({
  root: {
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: 100,
  },
  heading: {},
  todo: {
    backgroundColor: 'white'
  },
  panel: {
    flexWrap: 'wrap'
  },
  panelBody: {
    display: 'flex',
    justifyContent: 'space-between',
    padding: theme.spacing(3)
  },
  panelContent: {
    padding: theme.spacing(3)
  },
  description: {
    maxWidth: '600px',
    marginLeft: '20px',
    width: '100%',
  },
  contentDivider: {
    backgroundColor: 'rgba(0, 0, 0, 0.3)'
  },
  textArea: {
    width: '100%',
    fontSize: 18,
    fontWeight: 400
  },
  galleryContainer: {
    display: 'flex',
    flexWrap: 'wrap',
    alignSelf: 'fle-start',
    justifyContent: 'flex-start',
    width: 'calc(100% + 18px)',
    marginTop: 10,
    marginLeft: -9,
    marginRight: -9,
    borderWidth: 0,
    borderStyle: 'solid',
    borderColor: 'black',
    borderImage: 'initial',
  },
  galleryImage: {
    paddingLeft: 9,
    paddingRight: 9,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-start',
    alignItems: 'center',
    fontSize: 15,
    letterSpacing: 0.15,
    width: '30%',
    height: 300,
    marginBottom: '0.5rem',
    borderImage: 'initial',
  },
  galleryNewImage: {
    paddingLeft: 9,
    paddingRight: 9,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-start',
    alignItems: 'center',
    fontSize: 15,
    letterSpacing: 0.15,
    width: '30%',
    height: 300,
    marginBottom: '0.5rem',
    borderImage: 'initial',
    border: '2px solid rgba(0,0,0,0.5)',
  },
  galleryImg: {
    width: '100%',
    height: '100%',
    objectFit: 'cover'
  },
  galleryNewImg: {
    width: 40,
    height: 40,
    margin: 'auto'
  },
  updateButton: {
    display: 'block',
    marginLeft: 'auto',
    marginRight: theme.spacing(3)
  },
  photoImg: {
    paddingLeft: 9,
    paddingRight: 9,
    alignItems: 'center',
    fontSize: 15,
    letterSpacing: 0.15,
    marginBottom: '0.5rem',
  },
  photo: {
    width: '100%'
  },
  newPhoto: {
    width: '100%',
    maxWidth: 200,
  },
}));

const AntSwitch = withStyles((theme) => ({
  root: {
    width: 28,
    height: 16,
    padding: 0,
    display: 'flex',
  },
  switchBase: {
    padding: 2,
    color: theme.palette.grey[500],
    '&$checked': {
      transform: 'translateX(12px)',
      color: theme.palette.common.white,
      '& + $track': {
        opacity: 1,
        backgroundColor: theme.palette.primary.main,
        borderColor: theme.palette.primary.main,
      },
    },
  },
  thumb: {
    width: 12,
    height: 12,
    boxShadow: 'none',
  },
  track: {
    border: `1px solid ${theme.palette.grey[500]}`,
    borderRadius: 16 / 2,
    opacity: 1,
    backgroundColor: theme.palette.common.white,
  },
  checked: {},
}))(Switch);
function CheckItemDetail({
  checkItem,
  updateItem,
  onClose,
  open
}) {
  const classes = useStyles();

  const [description, setDescription] = useState(checkItem.description);
  const [form, setForm] = useState(checkItem.form);
  const [checked, setChecked] = useState(checkItem.checked);
  const [content, setContent] = useState(checkItem.content);
  const [gallery, setGallery] = useState(checkItem.gallery);
  const [files, setFiles] = useState([]);
  const [photo, setPhoto] = useState(checkItem.photo);
  const [file, setFile] = useState(null);
  const [updating, setUpdating] = useState(false);
  const pickerInput = useRef();
  const pickerImg = useRef();

  const getOptionLabel = (option) => {
    for (let i = 0; i < FORM_DESCRIPTIONS.length; i++) {
      if (FORM_DESCRIPTIONS[i].title === option) {
        return FORM_DESCRIPTIONS[i].content;
      }
    }
    return FORM_DESCRIPTIONS[0].content;
  };

  /** ******************************************************
   **   Table Part    *************************************
  ******************************************************** */

  const [columns, setColumns] = useState(TABLE_COLUMNS);

  const [tableData, setTableData] = useState(TABLE_DATA);

  const [tableColumnExtensions] = useState([
    { columnName: 'id', width: 60 },
  ]);

  const [editingRowIds, setEditingRowIds] = useState([]);
  const [addedRows, setAddedRows] = useState([]);
  const [rowChanges, setRowChanges] = useState({});

  const [inputValue, setInputValue] = useState('');
  const [chips, setChips] = useState(CHIPS);

  useEffect(() => {
    if (form === 'table' && checkItem.table_columns) {
      setChips(checkItem.table_columns);
    }

    if (form === 'table' && checkItem.table_data) {
      const table_data = [];
      for (let i = 0; i < checkItem.table_data.length; i++) {
        const row = {
          id: i,
        };
        for (let j = 0; j < checkItem.table_columns.length; j++) {
          const column = checkItem.table_columns[j];
          row[column] = checkItem.table_data[i][j];
        }
        table_data.push(row);
      }
      setTableData(table_data);
    }
  }, []);

  useEffect(() => {
    const columnArray = [];
    for (let i = 0; i < chips.length; i++) {
      columnArray.push({ title: chips[i], name: chips[i] },);
    }
    setColumns(columnArray);
  }, [chips]);

  const getRowId = (row) => row.id;

  const handleInputChange = (event) => {
    event.persist();
    setInputValue(event.target.value);
  };

  const handleInputKeyup = (event) => {
    event.persist();

    if (event.keyCode === 13 && inputValue) {
      if (!chips.includes(inputValue)) {
        setChips((prevChips) => [...prevChips, inputValue]);
        setInputValue('');
      }
    }
  };

  const handleChipDelete = (chip) => {
    setChips((prevChips) => prevChips.filter((prevChip) => chip !== prevChip));
  };

  const changeAddedRows = (value) => {
    const initialized = value.map((row) => (Object.keys(row).length ? row : {}));
    setAddedRows(initialized);
  };


  const commitChanges = ({ added, changed, deleted }) => {
    let changedRows;
    if (added) {
      const startingAddedId = tableData.length > 0 ? tableData[tableData.length - 1].id + 1 : 0;
      changedRows = [
        ...tableData,
        ...added.map((row, index) => ({
          id: startingAddedId + index,
          ...row,
        })),
      ];
    }
    if (changed) {
      changedRows = tableData.map((row) => (changed[row.id] ? { ...row, ...changed[row.id] } : row));
    }
    if (deleted) {
      const deletedSet = new Set(deleted);
      changedRows = tableData.filter((row) => !deletedSet.has(row.id));
    }
    setTableData(changedRows);
  };

  /** ******************************************************
   **   List Part    **************************************
  ******************************************************** */

  const [listCol] = useState(LIST_COLUMN);
  const [listData, setListData] = useState([]);

  const [listColumnExtensions] = useState([
    { columnName: 'id', width: 60 },
  ]);

  const [editingListRowIds, setEditingListRowIds] = useState([]);
  const [addedListRows, setAddedListRows] = useState([]);
  const [rowListChanges, setRowListChanges] = useState({});

  useEffect(() => {
    const column = LIST_COLUMN[0].name;
    if (form === 'list' && checkItem.list_data) {
      const list_data = [];
      for (let i = 0; i < checkItem.list_data.length; i++) {
        const row = {
          id: i,
        };
        row[column] = checkItem.list_data[i][0];
        list_data.push(row);
      }
      setListData(list_data);
    }
  }, []);

  const getListRowId = (row) => row.id;

  const changeAddedListRows = (value) => {
    const initialized = value.map((row) => (Object.keys(row).length ? row : {}));
    setAddedListRows(initialized);
  };


  const commitListChanges = ({ added, changed, deleted }) => {
    let changedRows;
    if (added) {
      const startingAddedId = listData.length > 0 ? listData[listData.length - 1].id + 1 : 0;
      changedRows = [
        ...listData,
        ...added.map((row, index) => ({
          id: startingAddedId + index,
          ...row,
        })),
      ];
    }
    if (changed) {
      changedRows = listData.map((row) => (changed[row.id] ? { ...row, ...changed[row.id] } : row));
    }
    if (deleted) {
      const deletedSet = new Set(deleted);
      changedRows = listData.filter((row) => !deletedSet.has(row.id));
    }
    setListData(changedRows);
  };

  /** ******************************************************
   **   Gallery Part    **************************************
  ******************************************************** */

  const _onFileInputHandler = (event) => {
    event.preventDefault();
    const _file = event.target.files[0];
    if (_file) {
      const url = URL.createObjectURL(_file);
      setFiles([...files, _file]);
      if (!gallery) {
        setGallery([url]);
      } else {
        setGallery([...gallery, url]);
      }
    }
  };

  const uploadImages = (checkedItem) => {
    if (files && files.length > 0) {
      setUpdating(true);
      const formData = new FormData();
      for (let i = 0; i < files.length; i++) {
        formData.append('file', files[i]);
      }

      axios
        .post(`${API_BASE_URL2}/${FILE.CRUD}`, formData)
        .then((response) => {
          if (response.data) {
            if (checkedItem.gallery) {
              checkedItem.gallery = [...checkedItem.gallery, ...response.data.urls];
            } else {
              checkedItem.gallery = response.data.urls;
            }
            updateItem(checkedItem);
          }
          setUpdating(false);
          onClose();
        });
    } else {
      updateItem(checkedItem);
      onClose();
    }
  };

  const uploadImage = (checkedItem) => {
    if (file) {
      setUpdating(true);
      const formData = new FormData();
      formData.append('file', file);

      axios
        .post(`${API_BASE_URL2}/${FILE.CRUD}`, formData)
        .then((response) => {
          if (response.data) {
            checkedItem.photo = response.data.urls[0];
            updateItem(checkedItem);
          }
          setUpdating(false);
          onClose();
        });
    } else {
      updateItem(checkedItem);
      onClose();
    }
  };

  const _onImageInputHandler = (event) => {
    event.preventDefault();
    const _file = event.target.files[0];
    if (_file) {
      const url = URL.createObjectURL(_file);
      setFile(_file);
      setPhoto(url);
    }
  };

  const onUpdateItem = () => {
    const checkedItem = { ...checkItem };
    checkedItem.description = description;
    checkedItem.form = form;

    switch (form) {
      case 'table':
        if (chips) {
          checkedItem.table_columns = chips;
        }
        if (tableData) {
          const table_data = [];
          for (let i = 0; i < tableData.length; i++) {
            const row = [];
            for (let j = 0; j < chips.length; j++) {
              const column = chips[j];
              const newColumn = tableData[i][column] || '';
              row.push(newColumn);
            }
            table_data.push(row);
          }
          checkedItem.table_data = table_data;
        }
        updateItem(checkedItem);
        onClose();
        break;
      case 'check':
        checkedItem.checked = checked;
        updateItem(checkedItem);
        onClose();
        break;
      case 'input':
        checkedItem.content = content;
        updateItem(checkedItem);
        onClose();
        break;
      case 'gallery':
        uploadImages(checkedItem);
        break;
      case 'list':
        if (listData) {
          const list_data = [];
          for (let i = 0; i < listData.length; i++) {
            const column = LIST_COLUMN[0].name;
            const newColumn = listData[i][column] || '';
            list_data.push([newColumn]);
          }
          checkedItem.list_data = list_data;
        }
        updateItem(checkedItem);
        onClose();
        break;
      case 'photo':
        uploadImage(checkedItem);
        break;
      default:
        console.log('default');
        break;
    }
  };

  return (
    <Dialog
      onClose={onClose}
      open={open}
      maxWidth="md"
      fullWidth
    >
      <Box className={classes.root}>
        <GridRow
          xs={12}
          item
          className={classes.panelBody}
        >
          <Autocomplete
            value={form}
            options={FORMS}
            getOptionLabel={(option) => getOptionLabel(option)}
            style={{ width: 300 }}
            renderInput={(params) => <TextField {...params} label="Form Type" variant="outlined" />}
            onChange={(event, value) => {
              setForm(value);
            }}
          />
          <TextField
            placeholder="Description"
            variant="outlined"
            label="Description"
            className={classes.description}
            defaultValue={description}
            onChange={(e) => {
              setDescription(e.target.value);
            }}
          />
        </GridRow>
        <Divider
          className={classes.contentDivider}
        />
        <Box className={classes.panelContent}>
          <Typography
            variant="body1"
            color="inherit"
          >
            Content
          </Typography>
          {(form === 'table') && (
          <>
            <GridRow
              xs={12}
              item
              className={classes.panelBody}
            >
              <Box
                p={2}
                display="flex"
                alignItems="center"
              >
                <Input
                  disableUnderline
                  fullWidth
                  className={classes.searchInput}
                  onChange={handleInputChange}
                  onKeyUp={handleInputKeyup}
                  placeholder="Enter a keyword"
                  value={inputValue}
                />
              </Box>
              <Divider />
              <Box
                p={2}
                display="flex"
                alignItems="center"
                flexWrap="wrap"
              >
                {chips.map((chip) => (
                  <Chip
                    className={classes.chip}
                    key={chip}
                    label={chip}
                    onDelete={() => handleChipDelete(chip)}
                  />
                ))}
              </Box>
              <Divider />
            </GridRow>
            <Box>
              <Grid
                rows={tableData}
                columns={columns}
                getRowId={getRowId}
              >
                <EditingState
                  editingRowIds={editingRowIds}
                  onEditingRowIdsChange={setEditingRowIds}
                  rowChanges={rowChanges}
                  onRowChangesChange={setRowChanges}
                  addedRows={addedRows}
                  onAddedRowsChange={changeAddedRows}
                  onCommitChanges={commitChanges}
                />
                <Table
                  columnExtensions={tableColumnExtensions}
                />
                <TableHeaderRow />
                <TableEditRow />
                <TableEditColumn
                  showAddCommand={!addedRows.length}
                  showEditCommand
                  showDeleteCommand
                />
              </Grid>
            </Box>
          </>
          )}
          {(form === 'check') && (
          <>
            <GridRow component="label" container alignItems="center" spacing={1}>
              <GridRow item>No</GridRow>
              <GridRow item>
                <AntSwitch
                  checked={checked}
                  onChange={(event) => { setChecked(event.target.checked); }}
                  name="checkedC"
                />
              </GridRow>
              <GridRow item>Yes</GridRow>
            </GridRow>
          </>
          )}
          {(form === 'input') && (
          <>
            <TextareaAutosize
              aria-label="minimum height"
              rowsMin={3}
              placeholder=""
              defaultValue={content}
              className={classes.textArea}
              onChange={(event) => { setContent(event.target.value); }}
            />
          </>
          )}
          {(form === 'list') && (
          <>
            <Box>
              <Grid
                rows={listData}
                columns={listCol}
                getRowId={getListRowId}
              >
                <EditingState
                  editingRowIds={editingListRowIds}
                  onEditingRowIdsChange={setEditingListRowIds}
                  rowChanges={rowListChanges}
                  onRowChangesChange={setRowListChanges}
                  addedRows={addedListRows}
                  onAddedRowsChange={changeAddedListRows}
                  onCommitChanges={commitListChanges}
                />
                <Table
                  columnExtensions={listColumnExtensions}
                />
                <TableHeaderRow />
                <TableEditRow />
                <TableEditColumn
                  showAddCommand={!addedRows.length}
                  showEditCommand
                  showDeleteCommand
                />
              </Grid>
            </Box>
          </>
          )}
          {(form === 'gallery') && (
          <Box className={classes.galleryContainer}>
            {gallery && (
              gallery.map((image, index) => (
                <Box
                  className={classes.galleryImage}
                  key={index}
                >
                  <img className={classes.galleryImg} src={image} alt="gallery" />
                </Box>
              ))
            )}
            <input
              ref={pickerInput}
              type="file"
              style={{
                display: 'none'
              }}
              onChange={(e) => { _onFileInputHandler(e); }}
            />
            <Box
              className={classes.galleryNewImage}
              onClick={() => {
                pickerInput.current.click();
              }}
            >
              <img className={classes.galleryNewImg} src={Images.PlusIcon} alt="Add New" />
            </Box>
          </Box>
          )}
          {(form === 'photo') && (
          <Box className={classes.galleryContainer}>
            <Box
              className={classes.photoImg}
              onClick={() => {
                pickerImg.current.click();
              }}
            >
              { photo ? (
                <img
                  className={classes.photo}
                  src={photo}
                  alt="Add New"
                />
              ) : (
                <img
                  className={classes.newPhoto}
                  src={Images.AddFileIcon}
                  alt="Add New"
                />
              )}
            </Box>
            <input
              ref={pickerImg}
              type="file"
              style={{
                display: 'none'
              }}
              onChange={(e) => { _onImageInputHandler(e); }}
            />
          </Box>
          )}
        </Box>
        <Box mt={1}>
          {updating
            ? (
              <Button
                size="small"
                variant="contained"
                ml={2}
                className={classes.updateButton}
                disabled
              >
                <CircularProgress
                  size={20}
                />
                {' '}
                Updating
              </Button>
            )
            : (
              <Button
                size="small"
                variant="contained"
                color="secondary"
                ml={2}
                className={classes.updateButton}
                onClick={() => {
                  onUpdateItem();
                }}
              >
                Update
              </Button>
            )}

        </Box>
      </Box>
    </Dialog>
  );
}

CheckItemDetail.propTypes = {
  checkItem: PropTypes.object,
  onClose: PropTypes.func,
  open: PropTypes.bool,
  updateItem: PropTypes.func,
};

CheckItemDetail.defaultProps = {
  open: false,
  onClose: () => {}
};

export default CheckItemDetail;
